type IconType =
    | 'triangle'
    | 'circle'
    | 'arrowUp'
    | 'upDown'
    | 'box'
    | 'hexa'
    | 'cross'

type SVGProps = {
    stroke?: boolean
    color?: string | number | any
    style?: object
    width: number
    icon: IconType
    left: string
    top: string
    hiddenMobile?: boolean
}

const SVG = ({
    stroke = false,
    color = ``,
    style,
    width,
    icon,
    left,
    top,
    hiddenMobile = false,
}: SVGProps) => {
    const id = sha256(
        `${stroke}${color}${width}${icon}${left}${top}${hiddenMobile}`
    )
    return color === 'icon_darker' || color === 'icon_darkest' ? (
        <svg
            style={style}
            sx={{
                position: `absolute`,
                stroke: stroke ? `currentColor` : `none`,
                fill: stroke ? `none` : `currentColor`,
                display: hiddenMobile ? hidden : `block`,
                color,
                width,
                left,
                top,
            }}
            viewBox={icons[icon].viewBox}
        >
            {icons[icon].shape}
        </svg>
    ) : (
        <svg
            sx={{
                position: `absolute`,
                stroke: stroke ? `currentColor` : `none`,
                fill: stroke ? `none` : `currentColor`,
                display: hiddenMobile ? hidden : `block`,
                filter: `url(#${id})`,
                color,
                width,
                left,
                top,
            }}
            viewBox={icons[icon].viewBox}
        >
            <defs>
                <filter id={id} x="0" y="0" width="200%" height="200%">
                    <feGaussianBlur
                        result="blurOut"
                        in="offOut"
                        stdDeviation="6"
                    />
                    <feBlend in="SourceGraphic" in2="blurOut" mode="normal" />
                </filter>
            </defs>
            {icons[icon].shape}
        </svg>
    )
}

export default SVG
